export const string = "<div class=\"pos-rel\"><div class=\"overflow pos-abs width-full\" [(elementHeightModel)]=\"elementHeightModel\" [ngClass]=\"wrapClass\" [class.no-scroll-bars]=\"!scrollBars && scrollBars!=null\"><ng-content></ng-content></div><div class=\"invisible\" [ngStyle]=\"{'height.px':elementHeightModel}\" style=\"overflow:hidden;\">&nbsp;</div></div>"